<template>
	<view class="image-detail" @touchstart="onTouchStart" @touchmove="onTouchMove" @touchend="onTouchEnd">
		<view class="image-container">
			<!-- <swiper :current="currentIndex" :indicator-dots="false" :autoplay="false" @change="swiperChange"> -->
				<movable-area scale-area="true" :style="{width: '100%'}">
					<!-- <swiper-item v-for="(image, index) in imageList" :key="index" > -->
						<movable-view :x="x" :y="y" direction="all" @change="onChange" scale="true" scale-min=1 scale-max=5 damping=50
							:style="{width: '100%'}">
							<!-- <image :src="image"></image> -->
							<image :src="imageList[0]"></image>
						</movable-view>
					<!-- </swiper-item> -->
				</movable-area>
			<!-- </swiper> -->
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				currentIndex: 0,
				imageList: [], // 图片链接数组
				touchStartX: 0,
				touchEndX: 0,
				scale: 1,
				x: 0,
				y: 0,
				old: {
					x: 0,
					y: 0
				}
			};
		},
		methods: {
			onChange: function(e) {
				this.old.x = e.detail.x
				this.old.y = e.detail.y
			},
			swiperChange(e) {
				this.currentIndex = e.detail.current;
			},
			onTouchStart(e) {
				this.touchStartX = e.touches[0].clientX;
			},
			onTouchMove(e) {
				if (this.imageList.length === 1) {
					return; // 只有一张图片时不执行左右滑动操作
				}

				const touchEndX = e.touches[0].clientX;
				const diffX = touchEndX - this.touchStartX;
				const currentImage = this.$refs['image'][this.currentIndex];

				if (currentImage) {
					currentImage.style.transform = `scale(${this.scale}) translateX(${diffX}px)`;
				}
			},
			onTouchEnd(e) {
				if (this.imageList.length === 1) {
					return; // 只有一张图片时不执行左右滑动操作
				}

				this.touchEndX = e.changedTouches[0].clientX;
				const diffX = this.touchEndX - this.touchStartX;
				const currentImage = this.$refs['image'][this.currentIndex];

				if (Math.abs(diffX) >= 50) {
					if (diffX > 0 && this.currentIndex > 0) {
						this.currentIndex--;
					} else if (diffX < 0 && this.currentIndex < this.imageList.length - 1) {
						this.currentIndex++;
					}
				}

				if (currentImage) {
					currentImage.style.transition = 'transform 0.3s';
					currentImage.style.transform = `scale(1) translateX(0)`;
					setTimeout(() => {
						currentImage.style.transition = '';
					}, 300);
				}
			},
		},
		onLoad(e) {
			// 在页面显示时预加载图片
			this.imageList = [e.preview_url]; // 你的图片链接数组
		},
	};
</script>

<style scoped>
	.image-detail {
		width: 100%;
		height: 100vh;
		background-color: #000;
		display: flex;
		align-items: center;
	}

	.image-container {
		width: 100%;
		height: 33%;
	}

	.image-container image {
		width: 100%;
	}
</style>